            Soul", un album concept de 42min, prévu pour une sortie vinyle. Les
            prises de sons ont lieu au studio The Blue Anvil Sound à Guipry et
            le mixage/mastering, au Garage Hermétique à Rezé. L'album propose de
            suivre l'histoire d'un être tourmenté par sa propre existence, sa
