t charismatique, le groupe déverse depuis quatre ans ses riffs puissants et saturées, ses rythmes tordus, ses mélodies psychédéliques et ses incantations vocales pour un rendu transcendant, véritable invitation au voyage. L’utilisation de la harpe électrique dans cette formation typée « rock » amène des sonorités uniques qui surprennent et renforcent l’univers ritualiste et mystique du groupe. ")])])]),_c('v-col',{staticClass:"mb-10 mt-10",style:(_vm.styleObjectColumn),attrs:{"cols":"12"}},[(_vm.$vuetify.breakpoint.mobile)?_c('v-img',{staticClass:"djiin-heading-image",style:(_vm.styleObjectMobileCover),attrs:{"src":require('../assets/images/cover2.jpg'),"center":"","width":"100%"}}):_c('v-img',{staticClass:"djiin-heading-image",staticStyle:{"object-fit":"contain"},attrs:{"src":require('../assets/images/cover2.jpg'),"center":"","contain":"","width":"100%"}})],1),_c('v-col',{staticClass:"mb-4 flex-container",attrs:{"cols":"12"}},[_c('div',{staticClass:"flex-column"},[_c('h2',{class:'djiin-section-titre djiin-section-titre--gros'},[_vm._v(" BIOGRAPHIE ")]),_c('p',{class:'djiin-text mb-5'},[_vm._v(" Après avoir enchainé les cafés-concerts et festivals locaux bretons pendant plus d’un an, les rennais de Djiin obtiennent une première partie pour le groupe Mars Red Sky au Mondo Bizarro, le 29 mars 2018, et se frayent ainsi une place dans la scène stoner/psyché française à laquelle ils se retrouvent affiliés. Djiin est programmé sur plusieurs tournées européennes aux côtés de groupes comme Cities Of Mars (SE), High Priestess (US), My Diligence (BE), Maidavale (SE), Stonebirds (FR), Dätcha Mandala (FR), The Riven (SE), Glowsun (FR), Liquid Orbit (DE), Appalooza (FR), The Necromancers (FR) et bien d’autres. ")]),_c('p',{class:'djiin-text mb-5'},[_vm._v(" En mars 2019, après deux albums live, le groupe sort son premier album studio \"The Freak\" chez More Fuzz Record. Les retours presse sont positifs et encourageant et les membres de djiin, notamment Chloé Panhaleux, chanteuse et harpiste du groupe, décident de s’investir d’avantage en tant qu’acteurs de leur scène. Les collaborations s’enchainent avec différentes associations et structures de booking, de production et d’évènementiel comme El Borracho Booking, Dead Pig, Celebration Days, No Name Agency, Echelon Talent Agency, Hellfest Production, Below The Sun, Crumble Fight, Métalorgie, Heavy Psych Sounds Records etc... Le tout dans une sphère indé portant des valeurs bienveillantes de partage, d’écoute et de liberté artistique sans concessions. ")]),_c('p',{class:'djiin-text mb-5'},[_vm._v(" En 2020, la crise du Covid 19 entraîne l’annulation de trois tournées en support band du groupe Gaupa, organisées par No Name Agency. Djiin retourne alors en studio pour enregistrer \"Meandering Soul\", un album concept de 42min, prévu pour une sortie vinyle. Les prises de sons ont lieu au studio The Blue Anvil Sound à Guipry et le mixage/mastering, au Garage Hermétique à Rezé. L'album propose de suivre l'histoire d'un être tourmenté par sa propre existence, sa personnalité et ses démons. Ainsi, l'auditeur voyage et rencontre chacun des protagonistes au fil de son écoute. Lui surviendront tantôt des paysages sombres et glacés, tantôt désertiques et hypnotisants, tantôt oppressants et sensuels, le tout naviguant sur des influences musicales variées : stoner, jazz, oriental, rock progressif 70's, doom et kraut pryschédélique. ")])])]),_c('v-col',{staticClass:"mb-4 flex-container",attrs:{"cols":"12"}},[_c('div',{staticClass:"flex-column"},[_c('h2',{class:'djiin-section-titre djiin-section-titre--gros'},[_vm._v(" Trombinoscope ")]),_vm._l((_vm.portraits),function(portrait){return _c('v-row',{key:portrait.name,staticClass:"flex-row djiin-portrait-container mt-10"},[(portrait.photoLeft && !_vm.$vuetify.breakpoint.mobile)?_c('v-col',{staticClass:"flex-column",attrs:{"cols":_vm.columnsNumber}},[_c('v-row',{staticClass:"flex-row flex-column--centered"},[_c('v-img',{class:'djiin-concert-img--' + portrait.ima